.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  @media screen and (min-width: 768px) {
    width: 635px;
    padding-left: 66px;
    padding-right: 66px;
    margin-right: auto;
    margin-left: auto;
  }
  @media screen and (min-width: 1280px) {
    width: 824px;
    padding-left: 228px;
    padding-right: 228px;
  }
}
